.App {
  text-align: center;
}

.App1{
  height: 220vh;
  background: #f0f2f5;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.ant-layout-content{
  height:100%;
}

.ant-table-container{
  padding: 20px;
  background: #f0f2f5;
}

.ant-table-container table{
  padding: 20px;
  background: #fff;
}

.ant-table-thead {
  background:#FFC20E;
}
